.header {
  font-family: "Open Sans", sans-serif;
  width: 100%;
  height: 100px;
  margin: 0;
  background: rgb(20, 0, 20);
  background: linear-gradient(
    0deg,
    rgba(20, 0, 20, 1) 35%,
    rgba(71, 0, 71, 1) 100%
  );
  &__title {
    font-family: "IBM Plex Sans Hebrew", sans-serif;
  }
}
.form__menu--list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
